import React from "react";
import { ErrorMessage, Field, useFormikContext } from "formik";
import camelCaseToTitleCase from "../../helpers/camel-case-to-title-case.helper";
import { InputTypes } from "./BaseInput.component";

type FormikFieldProps = {
  fieldName: any | string;
  type?: InputTypes;
} & React.HTMLAttributes<HTMLDivElement>;

const FormikField = ({ fieldName, type = "text", ...restProps }: FormikFieldProps) => {
  const formik = useFormikContext<{ [field: string]: string }>();
  const [hasError, setHasError] = React.useState(formik.errors[fieldName]);

  React.useEffect(() => {
    setHasError(formik.errors[fieldName]);
  }, [formik, fieldName]);
  return (
    <div className="flex flex-col gap-2">
      <label htmlFor={fieldName}>{camelCaseToTitleCase(fieldName)}</label>
      <Field
        type={type}
        id={fieldName}
        name={fieldName}
        className={`formik-field ${hasError ? "ring-red-500" : "ring-gray-300"}`}
        {...restProps}
      />
      <ErrorMessage component="span" className="text-red-500 text-xs" name={fieldName} />
    </div>
  );
};

export default React.memo(FormikField);
