import React from "react";
import useUserStore from "../../store/hooks/use-user-store.hook";
import { Formik, Form, Field } from "formik";
import UserFormFields from "../models/user-form-fields.type";
import FormikFieldComponent from "../../shared-components/inputs/FormikInputField.component";
import * as yup from "yup";
import FormikSelectFieldComponent from "../../shared-components/inputs/FormikSelectField.component";

const UserUpdateForm = ({ updateField }: { updateField: UserFormFields }) => {
  const { user } = useUserStore();

  const validationSchema = yup.object({
    username: yup.string().required("Username is required"),
    firstName: yup.string().required("First name is required"),
    lastName: yup.string().required("Last name is required"),
    gender: yup.string().required("Gender is required"),
    email: yup.string().email("Email is not valid").required("Email is required"),
  });
  const initialValues = {
    username: user?.username || "",
    firstName: user?.firstName || "",
    lastName: user?.lastName || "",
    gender: user?.gender || "",
    email: user?.email || "",
  };

  return (
    <div aria-roledescription="user update">
      <Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={() => {}} enableReinitialize>
        <Form className="flex flex-col gap-5">
          {renderField(updateField, "username")}
          {renderField(updateField, "firstName")}
          {renderField(updateField, "lastName")}
          <Field as="select" name="gender" className="formik-field">
            <option value="male">Male</option>
            <option value="female">Female</option>
          </Field>
          {renderField(updateField, "email")}
        </Form>
      </Formik>
    </div>
  );
};

const renderField = (updateField: UserFormFields, fieldName: UserFormFields) => {
  if (updateField !== fieldName) return null;

  if (fieldName === "gender") {
    return (
      <FormikSelectFieldComponent
        fieldName={fieldName}
        options={[
          { value: "male", label: "Male" },
          { value: "female", label: "Female" },
        ]}
      />
    );
  }

  return <FormikFieldComponent fieldName={fieldName} />;
};

export default React.memo(UserUpdateForm);
