import { Field } from "formik";
import React from "react";

type FormikSelectFieldProps = {
  fieldName: any | string;
  options: { value: string; label: string }[];
} & React.SelectHTMLAttributes<HTMLSelectElement>;

const FormikSelectField = ({ fieldName, options, ...restProps }: FormikSelectFieldProps) => {
  return (
    <Field as="select" name={fieldName} {...restProps}>
      {options.map(({ value, label }) => (
        <option key={value} value={value}>
          {label}
        </option>
      ))}
    </Field>
  );
};

export default React.memo(FormikSelectField);
